#qual-escolher{
    background-color: #1e1e1e;
    padding: 4rem 0;

    .qual-escolher-wrapper{
        .col-left{
            img{
                width: 100%;
            }
        }

        .col-right{
            color: white;
            
            h2 {
                font-family: $font-title;
                font-weight: $thin;
                text-transform: uppercase;
                margin-bottom: 2rem;
                font-size: 2.5rem;
            }
            
            // Accordion Styles
            .selo-accordion {
                .accordion-item {
                    background-color: #141414;
                    border-radius: 0.5rem;
                    margin-bottom: 1rem;
                    overflow: hidden;
                    transition: all 0.3s ease;
                    
                    .accordion-header {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        padding: 1.5rem 2rem;
                        cursor: pointer;
                        border-bottom: 0.0625rem solid rgba(255, 255, 255, 0.1);
                        transition: all 0.3s ease;
                        
                        &:hover {
                            background-color: rgba(255, 255, 255, 0.05);
                        }
                        
                        h3 {
                            font-family: $font-title;
                            font-weight: $thin;
                            text-transform: uppercase;
                            font-size: 1.5rem;
                            margin: 0;
                            color: white;
                            letter-spacing: 0.05em;
                        }
                        
                        .accordion-icon {
                            font-size: 1.5rem;
                            font-weight: 300;
                            color: white;
                            transition: transform 0.3s ease;
                            width: 2rem;
                            height: 2rem;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            border: 0.0625rem solid rgba(255, 255, 255, 0.3);
                            border-radius: 50%;
                        }
                    }
                    
                    .accordion-content {
                        max-height: 0;
                        opacity: 0;
                        overflow: hidden;
                        transition: all 0.4s ease;
                        
                        &.active {
                            max-height: 50rem;
                            opacity: 1;
                        }
                        
                        .accordion-body {
                            padding: 2rem;
                            
                            .indicado-para,
                            .o-que-avaliamos {
                                margin-bottom: 2rem;
                                
                                h4 {
                                    font-family: $font-title;
                                    font-weight: $thin;
                                    text-transform: uppercase;
                                    font-size: 1rem;
                                    color: rgba(255, 255, 255, 0.9);
                                    margin-bottom: 0.75rem;
                                    letter-spacing: 0.1em;
                                }
                                
                                p {
                                    color: rgba(255, 255, 255, 0.8);
                                    line-height: 1.6;
                                    margin: 0;
                                    font-size: 0.95rem;
                                }
                            }
                            
                            .ideal-para {
                                position: relative;
                                margin-top: 2rem;
                                border: 0.0625rem solid rgba(255, 255, 255, 0.2);
                                border-radius: 0.5rem;
                                
                                .ideal-para-tag {
                                    position: absolute;
                                    top: -0.75rem;
                                    left: 1.5rem;
                                    background-color: white;
                                    color: black;
                                    padding: 0.5rem 1.5rem 0.5rem 1rem;
                                    display: inline-block;
                                    z-index: 2;
                                    
                                    span {
                                        font-family: $font-title;
                                        font-weight: 700;
                                        font-size: 0.75rem;
                                        letter-spacing: 0.1em;
                                        text-transform: uppercase;
                                    }
                                    
                                    // Cut effect no canto inferior direito - igual ao teclab-title-box
                                    &::after {
                                        content: '';
                                        position: absolute;
                                        bottom: 0;
                                        right: 0;
                                        width: 0;
                                        height: 0;
                                        border-left: 0.75rem solid transparent;
                                        border-bottom: 0.75rem solid #141414;
                                        z-index: 1;
                                    }
                                }
                                
                                .ideal-para-content {
                                    background: linear-gradient(135deg, 
                                        rgba(0, 0, 0, 0.9) 0%, 
                                        rgba(0, 0, 0, 0.7) 50%,
                                        rgba(0, 0, 0, 0.5) 100%);
                                    padding: 2rem 1.5rem 1.5rem 1.5rem;
                                    border-radius: 0.5rem;
                                    
                                    p {
                                        color: rgba(255, 255, 255, 0.9);
                                        line-height: 1.6;
                                        margin: 0;
                                        font-size: 0.95rem;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

#por-que-confiar{
    background-color: #0f0f0f;
    padding: 4rem 0;
    color: white;
    padding-bottom: 0;

    .por-que-confiar-wrapper{
        padding-bottom: 4rem;
        .row {
            align-items: center;
            min-height: 30rem;
        }
        
        .col-left{
            background-image: url('../../assets/img/fire.png');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: top;
            padding: 3rem 2rem;
            position: relative;

            p{
                opacity: 0.8;
            }
            
            // Container principal - ttulo e badges lado a lado
            .header-content {
                display: flex;
                //align-items: center;
                align-items: baseline;
                justify-content: space-between;
                margin-bottom: 3rem;
                gap: 2rem;

                .header-content-title{
                    h2 {
                        font-family: $font-title;
                        font-weight: $thin;
                        text-transform: uppercase;
                        font-size: 2.5rem;
                        margin: 0;
                        line-height: 1.2;
                        letter-spacing: 0.05em;
                        flex: 0 0 auto;
                        white-space: nowrap;
                    }
                }
                
                .images-stack {
                    position: relative;
                    flex: 1;
                    height: 12rem;
                    
                    .image-item {
                        position: absolute;
                        transition: transform 0.3s ease;
                        
                        &:hover {
                            transform: scale(1.05) translateY(-0.5rem);
                            z-index: 10;
                        }
                        
                        img {
                            width: 180px;
                            height: auto;
                            filter: drop-shadow(0 0.5rem 1rem rgba(0, 0, 0, 0.3));
                        }
                        
                        // Posicionamento sobreposto - igual ao banner
                        &.image-1 {
                            top: 0;
                            right: 14rem;
                            z-index: 3;
                        }
                        
                        &.image-2 {
                            top: 1rem;
                            right: 7rem;
                            z-index: 2;
                        }
                        
                        &.image-3 {
                            top: 2rem;
                            right: -1rem;
                            z-index: 1;
                        }
                    }
                }
            }
            
            .brand-title {
                display: flex;
                justify-content: left;
                
                img {
                    width: auto;
                    height: auto;
                    filter: brightness(1.1) drop-shadow(0 0.5rem 1rem rgba(0, 0, 0, 0.4));
                }
            }
        }

        .col-right{
            opacity: 0.9;

            .ideal-para {
                position: relative;
                margin-top: 2rem;
                border: 0.0625rem solid rgba(255, 255, 255, 0.2);
                border-radius: 0.5rem;
                
                .ideal-para-tag {
                    position: absolute;
                    top: -0.75rem;
                    left: 1.5rem;
                    background-color: white;
                    color: black;
                    padding: 0.5rem 1.5rem 0.5rem 1rem;
                    display: inline-block;
                    z-index: 2;
                    
                    span {
                        font-family: $font-title;
                        font-weight: 700;
                        letter-spacing: 0.1em;
                        text-transform: uppercase;
                    }
                    
                    // Cut effect no canto inferior direito - igual ao teclab-title-box
                    &::after {
                        content: '';
                        position: absolute;
                        bottom: 0;
                        right: 0;
                        width: 0;
                        height: 0;
                        border-left: 0.75rem solid transparent;
                        border-bottom: 0.75rem solid #141414;
                        z-index: 1;
                    }
                }
                
                .ideal-para-content {
                    background: linear-gradient(135deg, 
                        rgba(0, 0, 0, 0.9) 0%, 
                        rgba(0, 0, 0, 0.7) 50%,
                        rgba(0, 0, 0, 0.5) 100%);
                    padding: 2rem 1.5rem 1.5rem 1.5rem;
                    border-radius: 0.5rem;
                    
                    p {
                        color: rgba(255, 255, 255, 0.9);
                        line-height: 1.6;
                        margin: 0;
                        font-size: 0.95rem;
                    }
                }
            }
        }
    }
    
    // Responsividade
    @media (max-width: 768px) {
        padding: 2rem 0;
        
        .por-que-confiar-wrapper {
            .row {
                min-height: auto;
            }
            
            .col-left {
                padding: 2rem 1rem;
                margin-bottom: 2rem;
                
                .header-content {
                    flex-direction: column;
                    align-items: center;
                    gap: 1.5rem;
                    margin-bottom: 2rem;
                    
                    h2 {
                        font-size: 2rem;
                        text-align: center;
                        white-space: normal;
                    }
                    
                    .images-stack {
                        height: 8rem;
                        
                        .image-item {
                            img {
                                width: 120px;
                            }
                            
                            // Posicionamento mobile - mais centralizado
                            &.image-1 {
                                top: 0;
                                right: 4rem;
                                z-index: 3;
                            }
                            
                            &.image-2 {
                                top: 0.5rem;
                                right: 2rem;
                                z-index: 2;
                            }
                            
                            &.image-3 {
                                top: 1rem;
                                right: 0;
                                z-index: 1;
                            }
                        }
                    }
                }
                
                .brand-title {
                    img {
                        width: 10rem;
                    }
                }
            }
            
            .col-right {
                
                
                
            }
        }
    }
}

